export default {

  created () {
    window.addEventListener('scroll', this.handleScroll)
    this.scrollTop2= ''
  },
  data () {
    return {
      scrollTop2: window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop,
      isBg: true,
      isOut: false,
      isUp: false
    }
  },
  methods: {
    handleScroll () {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop

      let h1 = 0
      let h2 = 75
      if (scrollTop === h1) {//顶部时隐藏导航栏
        this.isBg = false
     
      } if (scrollTop > h1) {//预定高度内显示
        this.isBg = true
      
        console.log(scrollTop);
      } if (scrollTop > h2) {//如果超出预定高度，让导航栏向上移出
        this.isOut = true
   
      } if (scrollTop > this.scrollTop2) {//如果下滚，取消导航栏恢复
        this.isUp = false
      
      } else {//否则如果上滚，导航栏恢复
        this.isUp = true
        console.log(scrollTop);
      }
      this.scrollTop2 = scrollTop



      // if (scrollTop > offsetTop) { console.log('scrollTop > offsetTop');
      // } else {console.log('scrollTop > offsetTop');
      // }
    }
  },
}

</script>

<style lang="scss" scoped>
header {
  background-color: #fff;
  height: 75px;
  width: 100%;
  background: none;
  transition: all 0.4s ease;
  font-size:40px;
  line-height: 1.5em;
  position: fixed;
  top: 0;
  &.bg {
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.15);
  }
  &.out {
    top: -100px;
  }
  &.up {
    top:0;

  }
}


